namespace $ {
	export class $hyoo_life_map extends $mol_plot_pane {
		
		/**
		 * ```tree
		 * gap_hor 0
		 * ```
		 */
		gap_hor() {
			return 0
		}
		
		/**
		 * ```tree
		 * gap_vert 0
		 * ```
		 */
		gap_vert() {
			return 0
		}
		
		/**
		 * ```tree
		 * zoom?val 16
		 * ```
		 */
		@ $mol_mem
		zoom(val?: any) {
			if ( val !== undefined ) return val as never
			return 16
		}
		
		/**
		 * ```tree
		 * scale $mol_vector_2d /
		 * 	<= zoom
		 * 	<= zoom
		 * ```
		 */
		@ $mol_mem
		scale() {
			const obj = new this.$.$mol_vector_2d(
				this.zoom(),
				this.zoom()
			)
			
			return obj
		}
		
		/**
		 * ```tree
		 * graphs / <= Points
		 * ```
		 */
		graphs() {
			return [
				this.Points()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * snapshot \
		 * ```
		 */
		snapshot() {
			return ""
		}
		
		/**
		 * ```tree
		 * snapshot_current \
		 * ```
		 */
		snapshot_current() {
			return ""
		}
		
		/**
		 * ```tree
		 * speed 0
		 * ```
		 */
		speed() {
			return 0
		}
		
		/**
		 * ```tree
		 * population 0
		 * ```
		 */
		population() {
			return 0
		}
		
		/**
		 * ```tree
		 * points_x /number
		 * ```
		 */
		points_x() {
			return [
			] as readonly number[]
		}
		
		/**
		 * ```tree
		 * points_y /number
		 * ```
		 */
		points_y() {
			return [
			] as readonly number[]
		}
		
		/**
		 * ```tree
		 * Points $mol_plot_dot
		 * 	diameter <= zoom
		 * 	series_x <= points_x
		 * 	series_y <= points_y
		 * ```
		 */
		@ $mol_mem
		Points() {
			const obj = new this.$.$mol_plot_dot()
			
			obj.diameter = () => this.zoom()
			obj.series_x = () => this.points_x()
			obj.series_y = () => this.points_y()
			
			return obj
		}
	}
	
}

