namespace $ {
	export class $hyoo_life extends $mol_page {
		
		/**
		 * ```tree
		 * title @ \Life of {population} cells
		 * ```
		 */
		title() {
			return this.$.$mol_locale.text( '$hyoo_life_title' )
		}
		
		/**
		 * ```tree
		 * plugins / <= Theme
		 * ```
		 */
		plugins() {
			return [
				this.Theme()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * head /
		 * 	<= Title
		 * 	<= Time
		 * 	<= Tools
		 * ```
		 */
		head() {
			return [
				this.Title(),
				this.Time(),
				this.Tools()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * tools /
		 * 	<= Lights
		 * 	<= Source_link
		 * 	<= Store_link
		 * ```
		 */
		tools() {
			return [
				this.Lights(),
				this.Source_link(),
				this.Store_link()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Head
		 * 	<= Map
		 * ```
		 */
		sub() {
			return [
				this.Head(),
				this.Map()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Theme $mol_theme_auto
		 * ```
		 */
		@ $mol_mem
		Theme() {
			const obj = new this.$.$mol_theme_auto()
			
			return obj
		}
		
		/**
		 * ```tree
		 * speed?val 0
		 * ```
		 */
		@ $mol_mem
		speed(val?: any) {
			if ( val !== undefined ) return val as never
			return 0
		}
		
		/**
		 * ```tree
		 * Slow $mol_icon_chevron_right
		 * ```
		 */
		@ $mol_mem
		Slow() {
			const obj = new this.$.$mol_icon_chevron_right()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Normal $mol_icon_chevron_double_right
		 * ```
		 */
		@ $mol_mem
		Normal() {
			const obj = new this.$.$mol_icon_chevron_double_right()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Fast $mol_icon_chevron_triple_right
		 * ```
		 */
		@ $mol_mem
		Fast() {
			const obj = new this.$.$mol_icon_chevron_triple_right()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Time $mol_switch
		 * 	value?val <=> speed?val
		 * 	options *
		 * 		1 <= Slow
		 * 		5 <= Normal
		 * 		1000 <= Fast
		 * ```
		 */
		@ $mol_mem
		Time() {
			const obj = new this.$.$mol_switch()
			
			obj.value = (val?: any) => this.speed(val)
			obj.options = () => ({
				1: this.Slow(),
				5: this.Normal(),
				1000: this.Fast()
			})
			
			return obj
		}
		
		/**
		 * ```tree
		 * Lights $mol_lights_toggle
		 * ```
		 */
		@ $mol_mem
		Lights() {
			const obj = new this.$.$mol_lights_toggle()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Source_link $mol_link_source uri \https://github.com/hyoo-ru/life.hyoo.ru
		 * ```
		 */
		@ $mol_mem
		Source_link() {
			const obj = new this.$.$mol_link_source()
			
			obj.uri = () => "https://github.com/hyoo-ru/life.hyoo.ru"
			
			return obj
		}
		
		/**
		 * ```tree
		 * store_link?val \
		 * ```
		 */
		@ $mol_mem
		store_link(val?: any) {
			if ( val !== undefined ) return val as never
			return ""
		}
		
		/**
		 * ```tree
		 * store_link_hint @ \Store snapshot
		 * ```
		 */
		store_link_hint() {
			return this.$.$mol_locale.text( '$hyoo_life_store_link_hint' )
		}
		
		/**
		 * ```tree
		 * Stored $mol_icon_stored
		 * ```
		 */
		@ $mol_mem
		Stored() {
			const obj = new this.$.$mol_icon_stored()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Store_link $mol_link_lazy
		 * 	uri_generated <= store_link?val
		 * 	hint <= store_link_hint
		 * 	sub / <= Stored
		 * ```
		 */
		@ $mol_mem
		Store_link() {
			const obj = new this.$.$mol_link_lazy()
			
			obj.uri_generated = () => this.store_link()
			obj.hint = () => this.store_link_hint()
			obj.sub = () => [
				this.Stored()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * snapshot \3~pab~paa~1ekj~0~hr8lxc~hr7wn5~hr8lxe
		 * ```
		 */
		snapshot() {
			return "3~pab~paa~1ekj~0~hr8lxc~hr7wn5~hr8lxe"
		}
		
		/**
		 * ```tree
		 * snapshot_current
		 * ```
		 */
		snapshot_current() {
			return this.Map().snapshot_current()
		}
		
		/**
		 * ```tree
		 * population
		 * ```
		 */
		population() {
			return this.Map().population()
		}
		
		/**
		 * ```tree
		 * Map $hyoo_life_map
		 * 	speed <= speed
		 * 	snapshot <= snapshot
		 * 	snapshot_current => snapshot_current
		 * 	population => population
		 * ```
		 */
		@ $mol_mem
		Map() {
			const obj = new this.$.$hyoo_life_map()
			
			obj.speed = () => this.speed()
			obj.snapshot = () => this.snapshot()
			
			return obj
		}
	}
	
}

